html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.burger {
  --burger-width: 30px;
  --burger-height: 30px;
  --burger-line-height: 2px;
  --shadow: rgba(0, 0, 0, 0.16) 0px 0px 2px, rgb(51, 51, 51) 0px 0px 0px 1px;
  position: fixed;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  top: 15px;
  right: var(--container-offset);
  cursor: pointer;
  color: #fff;
  border: none;
  background-color: transparent;
  z-index: 50;
}
@media (min-width: 1023px) {
  .burger {
    display: none;
  }
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: var(--burger-line-height);
  box-shadow: var(--shadow);
  background-color: currentColor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger::before {
  top: 0;
}
.burger::after {
  top: calc(100% - var(--burger-line-height));
}
.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transform: translateY(-50%);
  box-shadow: var(--shadow);
  transition: transform 0.3s ease-in-out;
}
.burger--active {
  --shadow: none;
}
.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}

.menu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 20px var(--container-offset);
  opacity: 0;
  pointer-events: none;
  transition: transform 0.5s ease, opacity 0.5s ease;
}
.menu--active {
  pointer-events: all;
  opacity: 1;
  transform: translateY(0%);
  background-color: #000;
}
.menu--inactive {
  transform: translateY(-100%);
}
@media (min-width: 1023px) {
  .menu {
    position: static;
    display: flex;
    align-items: center;
    gap: var(--header-gap);
    padding: 0;
    opacity: 1;
    pointer-events: all;
    transform: none;
  }
}
.menu .logo {
  display: block;
  text-align: center;
  margin-bottom: 40px;
}
@media (min-width: 767px) {
  .menu .logo {
    display: none;
  }
}
.menu .logo img {
  width: 44%;
  max-width: 200px;
}
.menu .nav__list {
  margin-bottom: 20px;
}
@media (min-width: 1023px) {
  .menu .nav__list {
    margin-bottom: 0;
  }
}

.header {
  --header-gap: 15px;
  background-color: #000;
  padding: 12px 0;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: var(--header-gap);
}
.header__container > .logo {
  max-width: 100px;
}
@media (min-width: 1023px) {
  .header__container > .logo {
    max-width: 206px;
  }
}

@media (min-width: 1023px) {
  .nav__list {
    margin-bottom: 0;
    display: flex;
    align-items: center;
  }
}

.nav__link {
  display: block;
  padding: 10px;
  text-align: center;
  color: #fff;
  font-weight: 600;
  font-size: 16px;
}

@media (min-width: 767px) {
  .why__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: 1fr;
    row-gap: 16px;
    column-gap: 16px;
  }
}
@media (min-width: 1023px) {
  .why__list {
    background-color: red;
  }
}

.socials {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.socials_light {
  --icon-color: #fff;
  --bg-color: #000;
}
.socials_dark {
  --icon-color: #000;
  --bg-color: #fff;
}

.socials__link {
  width: 30px;
  height: 30px;
  display: block;
  border-radius: 50%;
  background-color: var(--bg-color);
}
.socials__link svg {
  fill: var(--icon-color);
}