@mixin big-desktop {
  @media (min-width: (1439px)) {
    @content;
  }
}

@mixin tablet {
  @media (min-width: (1023px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (min-width: (767px)) {
    @content;
  }
}

@mixin mobile {
  @media (min-width: (575px)) {
    @content;
  }
}
